import Banner from '../../components/Banner'
import Collapse from '../../components/Collapse'
import BannerHome from '../../assets/banner.jpg'

function Apropos() {
    return (
      <div className="mainAbout">
        <Banner image={BannerHome} />
        <div className="drop">
          <Collapse
            titre="Fiabilit"
            description="Les annonces postes sur Kasa garantissent une fiabilit totale. Les photos sont conformes aux logements, et toutes les informations sont rgulirement vrifies  par nos quipes."
          />
          <Collapse
            titre="Respect"
            description="La bienveillance fait partie des valeurs fondatrices de Kasa. Tout comportement discriminatoire ou de perturbation du voisinage entranera une exclusion de notre plateforme."
          />
          <Collapse
            titre="Service"
            description="La bienveillance fait partie des valeurs fondatrices de Kasa. Tout comportement discriminatoire ou de perturbation du voisinage entranera une exclusion de notre plateforme."
          />
          <Collapse
            titre="Scurit"
            description="La scurit est la priorit de Kasa. Aussi bien pour nos htes que pour les voyageurs, chaque logement correspond aux critres de scurit tablis par nos services. En laissant une note aussi bien  l'hte qu'au locataire, cela permet  nos quipes de vrifier que les standards sont bien respects. Nous organisons galement des ateliers sur la scurit domestique pour nos htes."
          />
        </div>
      </div>
    )
  }
  
  export default Apropos
  