import React, { useState } from 'react'
import ArrowNext from '../../assets/VectorNext.png'
import ArrowPerv from '../../assets/VectorPerv.png'
import '../Carrousel/carrousel.css'

function Carrousel({ pictures }) {
  /**hook useState /cration variable d'tat / setCurrentImageIndex : MAJ de l'tat //index image initialis a 0 */
  const [currentImageIndex, setCurrentImageIndex] = useState(0)

  /*calcule le nombre total d'images du tableau d'images pass en prop.*/
  const totalImages = pictures.length

  /*fonction  qui incrmente l'index de l'image actuelle pour afficher l'image suivante*/
  /**(%) pour assurer que l'index reste dans les limites du tableau d'images.*/
  const nextSlide = () => {
    setCurrentImageIndex((prevIndex) => (prevIndex + 1) % totalImages)
  }

  /* fonction qui dcrmente l'index de l'image actuelle pour afficher l'image prcdente */

  const prevSlide = () => {
    setCurrentImageIndex(
      (prevIndex) => (prevIndex - 1 + totalImages) % totalImages,
    )
  }

  const imgCarrousel = (
    <img
      className="carousel-image"
      src={pictures[currentImageIndex]}
      alt={`pictures ${currentImageIndex + 1}/${totalImages}`}
    />
  )

  /**si pictures.length contient une seule image */
  if (totalImages <= 1) {
    return <div className="carousel-container">{imgCarrousel}</div>
  }

  return (
    <div className="carousel-container">
      <img
        className="arrow left-arrow"
        src={ArrowPerv}
        alt="Contenu prcednt"
        onClick={prevSlide}
      />
      {imgCarrousel}
      <img
        className="arrow right-arrow"
        src={ArrowNext}
        alt="Contenu suivant"
        onClick={nextSlide}
      />
      <div className="pagination">
        {currentImageIndex + 1}/{totalImages}
      </div>
    </div>
  )
}

export default Carrousel
